.logos {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    max-width: 790px;

    &__logo {

        width: 245px;
        height: 189px;
        border: 1px solid rgba(184, 184, 184, 0.5);
        border-radius: 5px;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        margin-bottom: 40px;
        flex-basis: 30%;
        transition: all .3s;
        color: $black;
        text-decoration: none;
        padding: 20px;

        &:hover {
            border: 1px solid #6b6b6b80;

        }


    }

    &__title {
        font-size: 18px;
        font-style: normal;
        font-weight: 500;
        line-height: 36px;
        letter-spacing: 0em;
        text-align: center;
        margin-bottom: 20px;


    }

    &__image {
        margin-bottom: 25px;
        height: 40px;

    }

    &__ic {
        width: 32px;
        height: 30px;
        fill: #fff;
        stroke: $black;

    }

    .cb__wrp {
        width: 32px;
        height: 30px;
        border: 2px solid #494a4a;
        border-radius: 5px;

    }

}

.save-settings--ekvairing {
    margin-top: 0;
}

@media only screen and (max-width : 1200px) {
    .top-part--ekvairing {
        margin-bottom: 24px;
    }

    .logos {

        &__logo {
            margin-bottom: 20px;
            flex-basis: 31.5%;

        }
    }

    .save-settings--ekvairing {
        margin-top: 0;
    }
}

@media only screen and (max-width : 760px) {


    .logos {
        &__logo {
            flex-basis: 48%;
            margin-bottom: 10px;
        }
    }

    .save-settings--ekvairing {
        margin-top: 10px;
    }



    @media only screen and (max-width : 560px) {
        .logos {
            &__logo {
                padding-top: 25px;
                height: auto;
            }

            &__img {
                margin-bottom: 10px;
            }
        }

        .logos {
            &__title {
                line-height: 1;
            }
        }


    }
}
