.top-part--profile {
    margin-bottom: 30px;
}

.blackpart--profile {
    padding: 37px 0 43px 0;
}




@media only screen and (max-width : 1200px) {
    .blackpart-imgs--profile {
        display: flex;
    }

    .top-part--profile {
        margin-bottom: 30px;
    }

    .blackpart--profile {
        padding: 50px 118px 125px 118px;
    }
}

@media only screen and (max-width : 600px) {
    .blackpart-imgs--profile {
        display: none;
    }

    .blackpart--profile {
        padding: 20px 15px 40px 15px;
    }

    .top-part--profile {
        margin-bottom: 10px;
    }
}
